import './ShopOverview.css'

import React from 'react'
import { Redirect } from 'react-router-dom'
import { Row } from 'reactstrap'

import ThemeSwitchProvider from '../../app/ThemeSwitchProvider'
import { useRoot } from '../../app/useRoot'
import BackgroundThemed from '../../common/BackgroundThemed'
import { ColStyleLeft, ColStyleRight } from '../../common/ColStyle'
import { i18n } from '../../i18n'
import { CartView } from '../cart/CartView'
import { FoodMenuView } from '../foodMenu/FoodMenuView'
import { OrderFormStorageNotice } from '../orderForm/OrderFormStorageNotice'
import { OrderFormView } from '../orderForm/OrderFormView'
import ShopHeaderView from './ShopHeaderView'

export const ShopOverview: React.FC = () => {
  const { order: orderModel } = useRoot()
  if (!orderModel.hasOrder) {
    return <Redirect to="/" />
  }
  return (
    <ThemeSwitchProvider branchId={orderModel.branchId}>
      <>
        <ShopHeaderView />
        <Row className="justify-content-md-center">
          <ColStyleLeft>
            <div className="shop__overview__column">
              <FoodMenuView />
            </div>
          </ColStyleLeft>

          <ColStyleRight>
            <div className="shop__overview__column cart__column">
              <div className="cart__column__header">
                <span className="flaticon-cart cart__column__header__icon" />
                <h4>{i18n.t`Your order`}</h4>
              </div>

              <BackgroundThemed
                className="cart__column__cart"
                imageName="backgroundCart"
              >
                <CartView />
              </BackgroundThemed>

              <OrderFormView />
            </div>
          </ColStyleRight>
          <OrderFormStorageNotice />
        </Row>
      </>
    </ThemeSwitchProvider>
  )
}
