import { Trans } from '@lingui/macro'
import React from 'react'
import { Route } from 'react-router-dom'
import { Row } from 'reactstrap'
import styled from 'styled-components/macro'

import { ColStyle } from '../../common/ColStyle'
import { QShopHeaderBranch, QShopHeaderIntro } from '../../graph/generated'
import { OrderAddress } from './OrderAddress'
import { ShopCloseAlert } from './ShopCloseAlert'
import { ShopClosingTimeModal } from './ShopClosingTimeModal'
import { ShopHeaderIcons } from './ShopHeaderIcons'
import { ShopIntro } from './ShopIntro'
import { ShopLogo } from './ShopLogo'
import { ShopOpeningHours } from './ShopOpeningHours'
import { Sticker } from './Sticker'

interface IProps {
  branch?: QShopHeaderBranch
  intro?: QShopHeaderIntro
  isLoading: boolean
}

const ShopHeaderStyled = styled.div`
  display: flex;
  min-height: 200px;
`

const ShopHeaderInfoStyled = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`

export const ShopHeader: React.FC<IProps> = ({ branch, intro, isLoading }) => (
  <div>
    <Row className="justify-content-md-center">
      <ShopHeaderIcons />
    </Row>

    <Row className="row-white justify-content-md-center">
      <ColStyle>
        <ShopHeaderStyled data-testid="shopHeader">
          {isLoading || !branch ? null : (
            <>
              <ShopLogo url={intro ? intro.homepageUrl : null} />

              <ShopHeaderInfoStyled>
                <Route
                  exact={true}
                  path="/"
                  render={() => (
                    <>
                      <Sticker>
                        <Trans>Choose your delivery option below</Trans>
                      </Sticker>
                      <ShopIntro intro={intro} />
                    </>
                  )}
                />
                <Route
                  path="/order"
                  render={() => (
                    <>
                      <ShopClosingTimeModal status={branch} />
                      <Sticker success={true}>
                        <OrderAddress address={branch.address} />
                      </Sticker>
                      <ShopOpeningHours branch={branch} />
                      <ShopCloseAlert status={branch} />
                      <ShopIntro intro={intro} />
                    </>
                  )}
                />
                <Route
                  path="/finish"
                  render={() => (
                    <>
                      <Sticker success={true}>
                        <Trans>Order was send</Trans>
                      </Sticker>
                      <ShopOpeningHours branch={branch} />
                      <ShopIntro intro={intro} />
                    </>
                  )}
                />
              </ShopHeaderInfoStyled>
            </>
          )}
        </ShopHeaderStyled>
      </ColStyle>
    </Row>
  </div>
)
