import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { useQOrderForm } from '../../graph/generated'
import { OrderAgreements } from './OrderAgreements'
import { OrderFormAutoFill } from './OrderFormAutoFill'
import { OrderFormInputs } from './OrderFormInputs'
import { OrderFormSetup } from './OrderFormSetup'
import { OrderPaymentMethod } from './OrderPaymentMethod'
import { OrderSubmitLine } from './OrderSubmitLine'

export const OrderForm = observer(() => {
  const { order: orderModel } = useRoot()

  const { data, loading } = useQOrderForm(
    { orderId: orderModel.orderId! },
    { skip: !orderModel.orderId },
  )

  const canSubmit =
    orderModel.isVamInitialized && !loading && data.order.countWares > 0

  return (
    <OrderFormSetup
      render={(form, isSubmitting) => (
        <div className="order__form">
          <div className="order__form__inner">
            <OrderFormAutoFill onFill={values => form.setAllValues(values)} />
            <OrderFormInputs />
            <OrderPaymentMethod />
            <OrderAgreements />
          </div>
          <OrderSubmitLine
            onClick={form.submitForm}
            canSubmit={canSubmit}
            isSubmitting={isSubmitting}
          >
            <Trans>Place order</Trans>
          </OrderSubmitLine>
        </div>
      )}
    />
  )
})
