import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { useQShopHeader } from '../../graph/generated'
import { ShopHeader } from './ShopHeader'

export const ShopHeaderView = observer(() => {
  const { order, business } = useRoot()
  const { data, loading } = useQShopHeader(
    {
      ...business.brandOrBranch,
      // need to use branchId from order first as it reflects branch for which
      // order is being made and it might have different eg. business hours
      // then the one obtained through business code
      branchId: order.branchId || business.branchId!,
    },
    { skip: !business.isValid },
  )

  return (
    <ShopHeader branch={data.branch} intro={data.intro} isLoading={loading} />
  )
})

export default ShopHeaderView
