import './ShopCloseAlert.css'

import React from 'react'
import { Alert } from 'reactstrap'

import { FCompanyBranchStatusFragment } from '../../graph/generated'
import { useShopStatus } from '../../hooks/useShopStatus'
import { i18n } from '../../i18n'

interface IProps {
  status: FCompanyBranchStatusFragment
}

export const ShopCloseAlert: React.FC<IProps> = ({ status }) => {
  const { decision, closingIn } = useShopStatus({
    status,
  })
  if (decision === 'ClosingSoon') {
    const closingInPlural = i18n.plural({
      value: closingIn,
      one: 'closing in 1 minute',
      few: 'closing in # minutes',
      other: 'closing in # minutes',
    })
    return (
      <Alert className="shop__header__alert">
        {i18n.t`Beware! We are ${closingInPlural}. Finish your order quickly.`}
      </Alert>
    )
  }
  return null
}
