import React from 'react'
import { Container } from 'reactstrap'
import styled from 'styled-components/macro'

import { FShopOpeningHours } from '../../graph/types'
import { formatTime } from '../../helpers/formats'
import { RM } from '../../helpers/ramda'
import { ShopStatusDisplay } from './ShopStatusDisplay'

interface IProps {
  branch: FShopOpeningHours.Fragment
}

const ShopOpeningHoursContainer = styled(Container)`
  margin: 0.4rem 0 0 0.1rem;
  display: flex;
`

const ShopOpeningHoursTimes = styled.div`
  padding-left: 2rem;
`

const ShopOpeningHoursStatus = styled.div`
  padding-left: 2rem;
`

const ShopOpeningHoursIcon = styled.div`
  margin: -0.2rem 0 0 -1rem;
  position: absolute;
`

export const ShopOpeningHours: React.FC<IProps> = ({ branch }) => {
  const openingHours = branch.businessHours.filter(RM.propEq('closed', false))
  return (
    <ShopOpeningHoursContainer fluid={true}>
      <ShopOpeningHoursIcon className="flaticon-clock" />
      {openingHours.length > 0 && (
        <ShopOpeningHoursTimes>
          {openingHours.map((businessHour, idx) => (
            <div data-testid="openingHours" key={idx}>
              {`${formatTime(businessHour.openAt)} - ${formatTime(
                businessHour.closeAt,
              )}`}
            </div>
          ))}
        </ShopOpeningHoursTimes>
      )}
      <ShopOpeningHoursStatus>
        <ShopStatusDisplay
          status={branch}
          deliveryTime={branch.defaultDeliveryTime}
          pickupTime={branch.defaultPickupTime}
        />
      </ShopOpeningHoursStatus>
    </ShopOpeningHoursContainer>
  )
}
